<template>
    <div class="container" id="loginBg" v-if="isReload">
      <div class="login">
        <div class="left">
          <img src="../../assets/img/login_img.jpg" alt="">
        </div>
        <div class="right">
          <h3>登录 <em>Login</em></h3>
          <el-form :model="user" :rules="rules" ref="loginForm">
            <el-form-item  prop="username">
              <div class="m-md-t">
                <el-input placeholder="用户名" v-model="user.username">
                  <template slot="prepend"><i class="iconfont icon-user"></i></template>
                </el-input>
              </div>
            </el-form-item>
            <el-form-item  prop="password">
              <div class="m-md-t">
                <el-input placeholder="密码" type="password" v-model="user.password" @keyup.enter.native="login('loginForm')">
                  <template slot="prepend">
                    <i class="iconfont icon-password"></i>
                  </template>
                </el-input>
              </div>
            </el-form-item>
            <el-form-item>
              <el-button @click="login('loginForm')"  type="primary">登录</el-button>
            </el-form-item>
           </el-form>
        </div>
      </div>
    </div>
</template>

<script>
export default {
  name: 'Login',
  data () {
    return {
      isReload: false,
      user: {
        username: '',
        password: ''
      },
      rules: {
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' }
          // { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'change' }
          // { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
        ]
      }
    }
  },
  created () {
    // 当注销登录回到登录页面时需要重置vuex全局状态
    if (this.$store.getters.menus.length > 0) {
      return window.location.reload()
    }
    console.log(this.$store.getters.menus.length)
    this.isReload = true
  },
  methods: {
    login (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$store.dispatch('Login', this.user).then(res => {
            if (res.result) {
              this.$router.push({path: '/admin'})
            } else {
              this.$message.error('用户名或密码错误')
            }
          }).catch(error => {
            console.log('登录出错', error)
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    }
  }
}
</script>

<style scoped>
  .container{
    background:url('../../assets/img/loginBg.jpg') no-repeat;
    width:100%;
    height: 100%;
    background-size: 100% 100%;
    display:flex;
    -webkit-align-items:center;
    align-items:center;
    -webkit-justify-content:center;
    justify-content:center;
  }
  .login{
    width: 868px;
    height: 428px;
    margin: 0 auto;
    position: relative;
    background: #fff;
  }
  .login .left{
    float: left;
  }
  .login h3{
    font-size: 27px;
    color: #1EA8F0;
    font-weight: normal
  }
  .login .right{
    float: left;
    padding: 10px 50px;
    width: calc( 50% - 152px);
    position: relative;
    top:50px;
  }
  .login h3 em{
    font-size: 19px;
    font-style: normal;
    color: #b8dcef;
  }
  .m-md-t{
    margin-top: 0;
  }
  .login .right .el-button{
    display: block;
    width: 100%;
    margin-top:20px;
  }
</style>



